// Statistics Chart Component
const StatisticsChart = ({ title, value, subtitle, chartType = 'line' }) => {
  const chartPath = "M 10 60 Q 30 40 50 50 Q 70 30 90 35 Q 110 45 130 25 Q 150 35 170 20 Q 190 30 210 15";

  return (
    <div className="bg-[#1A1F26] rounded-2xl p-4 sm:p-6 border border-gray-800/50">
      <h3 className="text-lg font-bold text-white mb-2">{title}</h3>
      <div className="text-3xl sm:text-4xl font-bold text-white mb-1">{value}</div>
      <p className="text-gray-400 text-sm mb-6">{subtitle}</p>
      
      {chartType === 'line' ? (
        <div className="h-20 sm:h-24 mb-4 relative">
          <svg className="w-full h-full" viewBox="0 0 220 80">
            <defs>
              <linearGradient id="chartGradient" x1="0%" y1="0%" x2="0%" y2="100%">
                <stop offset="0%" stopColor="#3B82F6" stopOpacity="0.3" />
                <stop offset="100%" stopColor="#3B82F6" stopOpacity="0" />
              </linearGradient>
            </defs>
            <path
              d={chartPath + " L 210 80 L 10 80 Z"}
              fill="url(#chartGradient)"
            />
            <path
              d={chartPath}
              stroke="#3B82F6"
              strokeWidth="2"
              fill="none"
              strokeLinecap="round"
              strokeLinejoin="round"
            />
          </svg>
        </div>
      ) : (
        <div className="h-20 sm:h-24 flex items-end justify-between gap-1 sm:gap-2 mb-4">
          {[12, 18, 15, 20].map((value, index) => (
            <div key={index} className="flex-1 flex flex-col items-center gap-1">
              <div
                className="w-full bg-gray-600 rounded-t-sm transition-all duration-300 hover:bg-gray-500"
                style={{ height: `${(value / 20) * 80}px` }}
              ></div>
            </div>
          ))}
        </div>
      )}
      
      <div className="flex justify-between text-xs text-gray-400">
        <span>Week 1</span>
        <span>Week 2</span>
        <span>Week 3</span>
        <span>Week 4</span>
      </div>
    </div>
  );
};

// Main Dashboard Component
export default function PingPongDashboard() {
  const [showNotifications, setShowNotifications] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');

  return (
    <div className="min-h-screen bg-[#0F1419] text-white">
      <Header 
        searchQuery={searchQuery}
        setSearchQuery={setSearchQuery}
        showNotifications={showNotifications}
        setShowNotifications={setShowNotifications}
        notifications={notifications}
      />

      {/* Main Dashboard Content */}
      <main className="p-3 sm:p-4 lg:p-6 max-w-7xl mx-auto">
        <div className="grid grid-cols-1 xl:grid-cols-3 gap-4 sm:gap-6">
          {/* Left Section */}
          <div className="xl:col-span-2 space-y-4 sm:space-y-6">
            <ProfileSection />
            <GameModeCards />
            <MatchHistory matchHistory={matchHistory} />
            <FriendsSection friends={friends} />
          </div>

          {/* Right Sidebar */}
          <div className="space-y-4 sm:space-y-6">
            <FriendSuggestions friendSuggestions={friendSuggestions} />
            <StatisticsChart 
              title="Win/Loss Rate" 
              value="60%" 
              subtitle="Last 30 Days" 
              chartType="line" 
            />
            <StatisticsChart 
              title="Matches Played" 
              value="20" 
              subtitle="Last 30 Days" 
              chartType="bar" 
            />
          </div>
        </div>
      </main>

      {/* Click outside to close notifications */}
      {showNotifications && (
        <div 
          className="fixed inset-0 z-40"
          onClick={() => setShowNotifications(false)}
        ></div>
      )}
    </div>
  );
}